'use client'

import { useState } from 'react'
import Link from 'next/link'
import { usePathname } from 'next/navigation'

const navItems = [
  { label: 'HOME', href: '/' },
  { label: 'BIO', href: '/texts' },
  { label: 'PORTFOLIO', href: '/selected-works' },
  { label: 'EXHIBITIONS', href: '/studio' },
  { label: 'CONTACT', href: '/private' },
]

export default function Navigation() {
  const pathname = usePathname()
  const [isOpen, setIsOpen] = useState(false)
  const isHome = pathname === '/'

  return (
    <>
      <nav style={{ 
        position: 'fixed', 
        top: 0, 
        left: 0, 
        right: 0, 
        zIndex: 120, 
        padding: '40px 60px',
        display: 'flex',
        justifyContent: 'flex-end',
        backgroundColor: 'transparent',
        pointerEvents: 'none'
      }}>
        {/* Hamburger Icon */}
        {!isOpen && (
          <button 
            onClick={() => setIsOpen(true)}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              display: 'flex',
              flexDirection: 'column',
              gap: '6px',
              pointerEvents: 'auto',
              padding: '10px'
            }}
          >
            <div style={{ width: '30px', height: '1px', backgroundColor: isHome ? '#fff' : '#000', transition: '0.3s' }} />
            <div style={{ width: '30px', height: '1px', backgroundColor: isHome ? '#fff' : '#000', transition: '0.3s' }} />
          </button>
        )}
      </nav>

      {/* Electric Blue Sidebar Menu (Jade Style) */}
      <div style={{
        position: 'fixed',
        top: 0,
        right: 0,
        width: '50vw', // Half screen width
        height: '100vh',
        backgroundColor: '#0000FF', // Pure Electric Blue
        zIndex: 150,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'center',
        paddingLeft: '10vw',
        transform: isOpen ? 'translateX(0)' : 'translateX(100%)',
        transition: 'transform 0.5s cubic-bezier(0.77, 0, 0.175, 1)',
        boxShadow: isOpen ? '-20px 0 60px rgba(0,0,0,0.2)' : 'none'
      }}>
        {/* Close button (X) */}
        <button 
          onClick={() => setIsOpen(false)}
          style={{
            position: 'absolute',
            top: '40px',
            right: '60px',
            background: 'none',
            border: 'none',
            cursor: 'pointer',
            padding: '10px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          {/* Simple X SVG */}
          <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M1 1L29 29M29 1L1 29" stroke="white" strokeWidth="1.5"/>
          </svg>
        </button>

        <ul style={{ 
          display: 'flex', 
          flexDirection: 'column',
          alignItems: 'flex-start',
          gap: '30px', 
          listStyle: 'none', 
          margin: 0, 
          padding: 0 
        }}>
          {navItems.map((item) => (
            <li key={item.href}>
              <Link
                href={item.href}
                onClick={() => setIsOpen(false)}
                style={{
                  fontSize: 'clamp(24px, 4vw, 40px)', // Scale with screen
                  letterSpacing: '0.2em',
                  fontWeight: 600,
                  textTransform: 'uppercase',
                  textDecoration: 'none',
                  color: '#ffffff',
                  fontFamily: 'ui-sans-serif, system-ui, -apple-system, sans-serif',
                  transition: 'opacity 0.3s ease',
                  opacity: pathname === item.href ? 1 : 0.8
                }}
              >
                {item.label}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      {/* Dimmer Overlay for background when menu is open */}
      {isOpen && (
        <div 
          onClick={() => setIsOpen(false)}
          style={{
            position: 'fixed',
            inset: 0,
            backgroundColor: 'rgba(0,0,0,0.2)',
            zIndex: 140,
            transition: 'opacity 0.5s ease'
          }}
        />
      )}
    </>
  )
}
